.sticky-button {
  #sticky {
    position: fixed;
    bottom: 15px;
    width: auto;
    z-index: 100; }

  .fixed {
    width: 100%;
    height: 50px;
    text-align: center;
    padding-top: 50px;
    background: #555;
    position: fixed;
    color: #FFF;
    transition: bottom .2s ease-out 0s; }

  .fixed-top {
    bottom: 0; }

  #sticky {
    min-width: 310px; }
  .border-1 {
    border: 1px solid #2E469D; }

  .border-2 {
    border: 1px solid #94268F; }

  .button-mob-status {
    display: block;
    background: #FFFFFF;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.15);
    border-radius: 6px;
    min-width: 145px;
    padding: 12px 0;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.15); } }


