header {
  position: relative;
  z-index: 101;
  .title-info {
    background-color: $dark-blue; }

  .lg-container {
    @media (min-width: 1024px) {
      max-width: 1024px; }
    font-family: "RobotoBold", sans-serif;
    font-weight: bold;
    line-height: 28px;
    color: $text-dark-blue; }

  .phone {
    span {
      font-family: "RobotoBold", sans-serif;
      font-weight: bold;
      font-size: 16px;
      line-height: 19px;
      color: $text-dark-blue; } }

  .contacts {
    .text-center {
      font-family: "RobotoRegular", sans-serif;
      font-weight: normal;
      font-size: 10px;
      line-height: 8px;
      color: #C1C0C0; } }

  .work-time {
    span {
      font-family: "RobotoRegular", sans-serif;
      font-size: 10px;
      line-height: 8px;
      color: #C1C0C0; }

    .days {
      span {
        font-family: "RobotoRegular", sans-serif;
        font-weight: 400;
        font-size: 10px;
        line-height: 12px;
        color: $black; } } }

  .menu {
    ul li a {
      font-family: "RobotoRegular", sans-serif;
      font-size: 12px;
      line-height: 24px;
      color: $black;
      width: 103%;
      height: 100%;
      display: block;
      //border-left: 1px solid #C1C0C0
      border-right: 1px solid #C1C0C0; } }


  .menu .home {
    background: url("../img/home-logo-main.svg") no-repeat 5% center;
 }    //padding-left: 1.8rem

  .menu {
    ul li:last-child a {
      color: $white; }

    ul .dropdown-content > a {
      border-right: 0px solid #C1C0C0; } }

  ul li {
    //border-left: 1px solid #C1C0C0
    width: 118px;
    margin: 0 3px; }

  ul li:first-child {
    border-left: 0px solid #C1C0C0; }

  ul li:nth-child(5) {
 }    //border-right: 1px solid #C1C0C0

  .menu .active {
    background: #d4eefb;
    color: #000;

 }    //dropdown-menu

  .dropbtn {
    color: $black;
    border: none; }


  .dropdown {
    position: relative;
    display: inline-block;
    z-index: 10; }


  .dropdown-content {
    display: none;
    position: absolute;
    background: transparent;
    top: 32px;
    padding-top: 5px;
    min-width: 118px;
    //box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2)
    z-index: 0; }


  .dropdown-content a {
    color: black;
    background-color: #d4eefb;
    text-decoration: none;
    display: block; }

  .dropdown-content a:hover {
    background-color: #ddd; }

  .dropdown:hover .dropdown-content {
    display: block; }
  //.dropdown-content:hover
  //  display: block

  .dropdown:hover .dropbtn {
 }    //background-color: #d4eefb

  .fa {
    position: absolute;
    color: #9113AC;
    bottom: -7px;
    right: -2px;
    transform: rotate(315deg); }

  .triangle-bottomright {
    position: absolute;
    bottom: 1px;
    right: 1%;
    width: 0;
    height: 0;
    border-bottom: 10px solid #9113AC;
    border-left: 10px solid transparent; }

  .relative.top-0 {
    top: 0;
    @media (min-width: 320px) {
      left: -25px; }
    @media (min-width: 768px) {
      left: -15px; } } }

