#bg2 {
  display: block; }
.modal-content2 {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #ffffff;
  margin: auto;
  border: 0px solid #7B1FA2;
  width: 350px;
  box-shadow: 0 4px 4px 0 rgba(0,0,0,0.25);
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.5s;
  animation-name: animatetop1;
  animation-duration: 0.5s;
  z-index: 1500;
  img {
    max-width: 39px; }
  @media (max-width: 350px) {
    width:  280px; }
  input {
    font-family: "RobotoRegular", sans-serif;
    color: black; }

  input::placeholder {
    font-size: 9px;
    font-family: "RobotoRegular", sans-serif;
    line-height: 11px;
    color: black;
    text-transform: uppercase;
    @media (min-width: 768px) {
      font-size: 9px; } }

  textarea::placeholder {
    font-size: 9px;
    font-family: "RobotoRegular", sans-serif;
    line-height: 11px;
    color: #000000;
    text-transform: uppercase;
    @media (min-width: 768px) {
      font-size: 9px; } }

  textarea {
    font-size: 9px;
    font-family: "RobotoRegular", sans-serif;
    line-height: 11px;
    color: #000000; }
  @media (min-width: 768px) {
    font-size: 9px; }

  .modal-body img {
    height: 32px; }

  .items-start img {
    width: 32px; }

  .close-btn2 {
    display: none;
    color: white;
    float: right;
    font-size: 28px;
    font-weight: bold; }

  .close-btn2:hover,
  .close-btn2:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer; }

  @media (min-width: 768px) {
    @keyframes animatetop1 {
      from {
        top: 350px;
        opacity: 0; }
      to {
        top: 0;
        opacity: 1; } } } }

.center-modal .modal-content,
.center-modal .modal-content2 {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }





