.modal {
  display: block;
  position: fixed;
  z-index: 101;
  padding-top: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4); }

.modal-content {
  display: block;
  position: absolute;
  top: 30px;
  right: 0px;
  background-color: #ffffff;
  margin: auto;
  border: 0px solid #7B1FA2;
  width: 350px;
  box-shadow: 0 4px 4px 0 rgba(0,0,0,0.25);
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: animatetop;
  animation-duration: 0.4s;
  z-index: 300;
  img {
    max-width: 39px; }
  @media (max-width: 350px) {
    width:  280px; }

  input {
    font-family: "RobotoRegular", sans-serif;
    color: black; }

  input::placeholder {
    font-size: 9px;
    font-family: "RobotoRegular", sans-serif;
    line-height: 11px;
    color: black;
    text-transform: uppercase;
    @media (min-width: 768px) {
      font-size: 9px; } }

  .modal-body img {
    height: 32px;
    width: 32px; }

  .modal-header {
    background-color: #ffffff; }

  .close-btn {
    display: none;
    color: white;
    float: right;
    font-size: 28px;
    font-weight: bold; }

  .close-btn:hover,
  .close-btn:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer; }

  //@-webkit-keyframes animatetop
  //  from
  //    top: 300px
  //    opacity: 0
  //  to
  //    top: 30px
  //    opacity: 1


  @media (min-width: 768px) {
    @keyframes animatetop {
      from {
        top: 300px;
        opacity: 0; }
      to {
        top: 30px;
        opacity: 1; } } } }

